
I apologize for the confusion. The error you're seeing (vue/no-multiple-template-root) is because the Vue template requires a single root element, and it seems the template structure has changed. Let's fix this by wrapping the content in a single root element. Here's an updated version:

html
Copy code
<template>
  <div class="cooperatives-list">
    <table>
      <thead>
        <tr class="cooperative-header">
          <th>Name</th>
          <th>VAT</th>
          <th>Details</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="cooperative in cooperatives" :key="cooperative.id" class="cooperative-item">
          <td>{{ cooperative.name }}</td>
          <td>{{ cooperative.vat }}</td>
          <td>
            <router-link :to="{ name: 'CooperativeDetails', params: { id: cooperative.id }}">
              View Details
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      cooperatives: [],
    };
  },

  created() {
    this.fetchCooperatives();
  },

  methods: {
    fetchCooperatives() {
      const accessToken = localStorage.getItem('jwt');

      axios.get('http://localhost:9090/api/cooperative', {
        headers: {
          'Authorization': 'Bearer ' + accessToken,
        },
      })
        .then(response => {
          this.cooperatives = response.data;
        })
        .catch(error => {
          console.error('Error fetching cooperatives', error);
        });
    },
  },
};
</script>



<style scoped>
.cooperatives-list {
  padding: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

