  font-weight: bold;
}

.cooperative-header th,
.cooperative-item td {
  border: 1px solid #dee2e6;
  padding: 10px;
  text-align: left;
}

.cooperative-item td {
  background-color: #f8f9fa;
}

.router-link {
  text-decoration: none;
  color: #007bff;
  cursor: pointer;
}

.router-link:hover {
  text-decoration: underline;
}
</style>