<!-- src/views/SignUp.vue -->
<template>
  <div>
    <h1>Sign Up</h1>
    <!--router-link to="/login">Login</router-link-->
    <form @submit.prevent="registerUser">
      <label for="username">Username:</label>
      <input type="text" v-model="username" required>
      <label for="email">Email:</label>
      <input type="email" v-model="email" required>
      <label for="password">Password:</label>
      <input type="password" v-model="password" required>
      <button class="signUpButton" type="submit">Sign Up</button>
    </form>
    <strong><div v-if="submissionCompleted">Signed Up - Go to <router-link to="/login">Login</router-link></div></strong>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      username: '',
      email: '',
      password: '',
      submissionCompleted: false,

    };

  },
  methods: {
    registerUser() {
      axios.post('http://localhost:9090/api/auth/signup', {
        username: this.username,
        email: this.email,
        password: this.password,
      })
          .then((response) => {
            console.log(response.data);
            this.submissionCompleted = true;
            // Handle successful registration response (e.g., show success message)
          })
          .catch((error) => {
            console.error(error);
            // Handle registration error (e.g., show error message)
          });
    },
  },
};
</script>


<style scoped>
.signUpButton {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: #333;
  cursor: pointer;
}





</style>