<template>
  <div>
    
    <p v-if="cooperativeDetails.name">Name: {{ cooperativeDetails.name }}</p>
    <p v-if="cooperativeDetails.status">Status: {{ cooperativeDetails.status }}</p>
    <p v-if="cooperativeDetails.notes">Notes: {{ cooperativeDetails.notes }}</p>
    <p>User ID: {{ userDetails.id }}</p>







    <p v-if="approvedNotes">Notes: {{ approvedNotes }}</p>
    <div v-if="cooperatives.length">
      <h2>Cooperatives:</h2>
      <ul>
        <li v-for="coop in cooperatives" :key="coop.id">{{ coop.name }} - {{ coop.status }} - {{ coop.notes }}</li>


      </ul>

    </div>
  </div>
</template>

<script>
import axios from 'axios';



export default {
  data() {
    return {
      cooperativeDetails: {},
      userDetails: {},
      cooperatives: [],
      approvedStatus: '',
      approvedNotes: '',
    };
  },

  async created() {
    await this.fetchUserDetails();
    this.fetchCooperatives();




  },

  methods: {
    async fetchUserDetails() {
      const storedUser = localStorage.getItem('user');
      if (storedUser) {
        this.userDetails = JSON.parse(storedUser);
      } else {
        return null;
      }
    },

    async fetchCooperatives() {
      const accessToken = localStorage.getItem('jwt');


      if (!this.userDetails.id) {
        console.error('User ID is undefined.');
        return;
      }

      const userId = this.userDetails.id;

      try {
        const response = await axios.get(`http://localhost:9090/api/cooperative/user/${userId}/cooperatives/details`, {
          headers: {
            'Authorization': 'Bearer ' + accessToken,
          },
        });

        this.cooperatives = response.data;


      } catch (error) {
        console.error('Error fetching cooperatives', error);
      }
    }
  },


  beforeRouteEnter(to, from, next) {
    // Access the component instance via `vm`
    next(vm => {
      // If there are query parameters, set them in the component's data
      if (to.query.status) {
        vm.approvedStatus = to.query.status;
      }
      if (to.query.notes) {
        vm.approvedNotes = to.query.notes;
      }
    });
  },

};
</script>

